body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background: url(/apps/pool-app/static/media/background.cf6c0d50.jpg) no-repeat center center fixed;
  background-size: cover;
  color: white;
}

header {
  background-color: rgba(125, 204, 200, 0.8);
  color: white;
  padding-top: 1em;
  padding-bottom: 0.5em;
}

.App {
  text-align: center;
}

.block--margin-top {
  margin-top: 3em;
}

.flex-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}

ul {
  margin: 0;
  padding: 0;
}

ul li {
  margin: 1em;
  padding: 3em;
  color: white;
  font-weight: bold;
  background-color: rgba(251, 126, 42, 0.5);
  list-style-type: none;
}

.leaderboard {
  background-color: rgba(125, 204, 200, 0.8);
  color: black;
  font-weight: bold;
  border-radius: 0.5em;
  padding: 1em;
  border: 0.5em solid gold;
}
